@font-face {
  font-family: 'Chalet';
  font-style: normal;
  font-weight: normal;
  src: url(./../fonts/Chalet.eot);
  src: url(./../fonts/Chalet.eot) format("embedded-opentype"),url(./../fonts/Chalet.woff) format("woff"),url(./../fonts/Chalet.ttf) format("truetype"),url(./../fonts/Chalet.svg) format("svg");
}

html {
  color: #FFF;
  background-color: #2B3A42;
  background-repeat: repeat;
  background-image: url("./../img/webpack-cube.svg");
  background-position: center;
  background-attachment: fixed;
  background-size: 10%;
}

img {
  display: block;
  max-width: 300px;
  height: auto;
}

#app {
  margin: auto;
  padding: 2rem;
  width: 80%;
  min-height: 100vh;
  text-align: center;
  background-color: rgba(43,58,66,0.9);
}

#app * {
  margin: 2rem auto;
}

.Menu {
  display: flex;
  justify-content: space-evenly;
}

.Menu a {
  color: #8dd6fb;
  transition: all .3s ease;
}

.Menu a:hover {
  opacity: .75;
}

